.button {
	padding: 0.65em 1.5em;
	font-family: $font-secondary;
	font-display: swap;
	font-size: 1rem;
	letter-spacing: 0.07em;
	color: #000;
	background-color: transparent;
	border: 2px solid currentColor;
	cursor: pointer;
	transition: 0.2s ease;

	@media (min-width: $screen-lg-min) {
		font-size: 1.286rem;
	}

	@media (min-width: 1600px) {
		font-size: 1.429rem;
	}

	&--hidden {
		display: none;
		opacity: 0;
	}

	&--visible-lg {
		display: none;

		@media (min-width: $screen-lg-min) {
			display: inline-block;
		}
	}

	&--faded-in {
		animation: FadeIn 0.5s ease;
	}

	&--small {
		font-size: 0.7rem;
		border-width: 1px;

		@media (min-width: $screen-md-min) {
			font-size: 0.9286rem;
			border-width: 2px;
		}

		@media (min-width: $screen-lg-min) {
			font-size: 1rem;
		}
	}

	&--close {
		height: 15px;
		width: 15px;
		padding: 0;
		background-color: transparent;
		background-image: url('/images/icons/close-icon.svg');
		background-size: 100%;
		background-repeat: no-repeat;
		border: none;
		border-radius: 0;
		cursor: pointer;

		&:hover {
			opacity: 0.8;
		}

		&:focus {
			opacity: 0.7;
			outline: none;
		}
	}

	&--underline {
		padding: 2px;
		font-family: $font-main;
		font-size: 1rem;
		letter-spacing: inherit;
		border: none;
		border-bottom: 1px solid #000;
	}
	
	&:focus,
	&:hover {
		opacity: 0.8;
		outline: none;
	}

	&:active {
		opacity: 0.7;
	}

	@include color-range();
}

@keyframes FadeIn{
	from {
		opacity: 0;
	}

	to {
		opacity: 1;
	}
}