.slider {
	flex: 1;
	overflow: hidden; // To prevent infinite width with using flexbox wrapper //

	&.slick-dotted {
		margin-bottom: 0;
	}
	
	&--advantages {
	    margin: 0 -2rem;
	}

	&--promo {
		.slick-dots {
			bottom: 6vh;

			li button:before {
				font-size: 35px;
				opacity: 1;
			}

			li.slick-active button:before {
				color: $color-green;
				opacity: 1;
			}
		}

		.slick-list,
		.slick-slide,
		.slick-track,
		.slick-slide > div,
		.wrapper,
		.container-fluid,
		.row {
			height: 100%;
		}
	}

	&--pricelist {
		position: relative;
		display: flex;
		align-items: center;

		.slick-list {
			@media (min-width: $screen-lg-min) {
				padding: 0 40px;
				mask-image: linear-gradient(to right, transparentize(#000, 1) 0%, #000 7%, #000 93%, transparentize(#000, 1) 100%);
			}
		}

	    .slick-track {
	    	display: flex;
	    }

	    .slick-slide {
	    	height: auto;

	    	& > div {
	    		height: 100%;
	    	}
	    }

	    .slick-dots {
			left: 0;
			right: 0;
			bottom: -35px;
			width: auto;

	    	li button:before {
	    		font-size: 38px;
	    		opacity: 1;
	    	}

	    	li.slick-active button:before {
	    		color: $color-orange;
	    	}
	    }

	    .slider__item {
	    	display: flex !important;
	    }

	    .card__title,
	    .card__footer {
	    	opacity: 0;
	    	transition: 0.2s ease;

	    	@media (min-width: $screen-lg-min) {
	    		opacity: 1;
	    	}
	    }

	    .slick-current {
	    	.card__title,
	    	.card__footer {
	    		opacity: 1;
	    	}
	    }
	}

	&__background {
		content: '';
		position: absolute;
		left: 0;
	    top: 28vh;
		width: 600%;
		transition: 0.48s ease;

		@media (min-width: $screen-md-min) {
			top: 13vh;
		}

		@media (min-width: $screen-lg-min) {
			width: 200%;
		}

		@media (min-width: 1600px) {
			top: 20vh;
		}
	}

	&__content-wrap {
		display: flex;
	    align-items: flex-start;

	    @media (min-width: $screen-md-min) {
	    	align-items: center;
	    }
	}

	&__text {
		max-height: 34vh;
		overflow-y: scroll;

		p:first-child {
			margin-top: 0;
		}

		@media (min-width: $screen-lg-min) {
			max-height: none;
			overflow: auto;
		}
	}

	&__item {
		height: 100%;
		background-size: auto 50%;
		background-position: left bottom;
		background-repeat: no-repeat;

		@media (min-width: $screen-md-min) {
			background-size: auto 100%;
		}

		&--first {
			background-image: url('/images/slides/03-01@2x.jpg');
		}

		&--second {
			background-image: url('/images/slides/03-02@2x.jpg');
		}

		&--third {
			background-image: url('/images/slides/03-03@2x.jpg');
		}

		&--fourth {
			background-image: url('/images/slides/03-04@2x.jpg');
		}
	}
}