.modal {
	display: none;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	width: 100%;
	font-size: 1rem;
	background-color: transparentize(#000, 0.3);
	z-index: 100;
	transition: 0.2s ease;

	&--visible {
		display: flex;
		justify-content: center;
		align-items: center;
		animation: ShowModal 0.2s ease;
	}

	&__wrapper {
		flex: 1;
	}

	&__window {
		position: relative;
		min-height: 70vh;
    max-height: 90vh;
		max-width: 760px;
		padding: 0.7em 1.5em;
		margin: 0 auto;
		background-color: #fff;

		@media (min-width: $screen-md-min) {
			min-height: 50vh;
			padding: 1.429em 2.857em;
		}

		// &--wide {
		// 	max-width: 850px;
		// }
	}

	&__title {
		margin-top: 2.857rem;
	}

	&__close {
		position: absolute;
		right: 1.5rem;
		top: 1.5rem;

		@media (min-width: $screen-md-min) {
			right: 2.857rem;
			top: 2.857rem;
		}
	}

	&__image {
		position: absolute;
		left: 0;
	    bottom: -12px;
		transform: translateX(-20%);
		height: 40%;
		max-height: 140%;

		@media (min-width: $screen-md-min) {
			height: 530px;
			transform: translateX(-50%);
		}

		@media (min-width: 1600px) {
			height: 640px;
			transform: translateX(-50%);
		}

		&--centered {
			@media (min-width: $screen-md-min) {
				bottom: 50%;
				transform: translate(-50%, 50%);
			}
		}
	}

	&__block {
		&--visible-md {
			display: none;

			@media (min-width: $screen-md-min) {
				display: block;
			}
		}

		&--hidden-md {
			display: block;

			@media (min-width: $screen-md-min) {
				display: none;
			}
		}
	}

	&__promo {
		margin: 20px 0 20px;
		padding: 20px 0;
		border-top: 1px solid $color-gold;
		border-bottom: 1px solid $color-gold;

		@media (min-width: $screen-md-min) {
			margin: 60px 0 20px;
			padding: 35px 0;
		}

		&--moved-top {
			margin-top: 20px
		}
	}

	&__promo-title {
		font-size: 1.5rem;

		@media (min-width: $screen-md-min) {
			font-size: 2.714rem;
		}
	}

	&__footer {
		display: flex;
	    justify-content: center;

		&--column {
			flex-direction: column;
		}
	}

	&__button {
		margin: 0 10px;

		&--permission {
			display: block;
		    margin: 10px auto;
		}

		&--shifted {
			margin: 30px 0;
		}
	}

	&__block-overflow {
		max-height: 45vh;
		margin-right: -15px;

		@media (min-width: 1600px) {
			max-height: 30vh;
		}

		.simplebar-track.vertical {
			right: 2px;
			width: 1px;
		    background-color: transparentize($color-gold, 0.6);
		}

		.simplebar-content {
			padding-right: 15px;
		}

		.simplebar-scrollbar.visible {
			right: -1px;
			width: 3px;
			background-color: $color-gold;
			opacity: 1;
		}
	}
}

@keyframes ShowModal {
	from {
		opacity: 0;
	}

	to {
		opacity: 1;
	}
}