.navbar {
	position: fixed;
	z-index: 10;
	right: -15px;
	top: 50%;
	display: none;
	transform: translateY(-50%);
	transition: 0.2s ease;

	&:hover,
	&:focus {
		transform: translateY(-50%) translateX(-20px);

		@media (min-width: $screen-xxl-min) {
			transform: translateY(-50%) translateX(-35px);
		}
	}

	@media (min-width: $screen-lg-min) {
		display: block;
	}
	
	&__list {
		margin: 0;
		padding: 0;
		list-style: none;
	}

	&__link {
		display: block;
		width: 45px;

		@media (min-width: $screen-xxl-min) {
			width: 65px;
		}

		svg {
			width: 100%;
		}
	}

	&__item {
		display: block;
		margin: 5px;
		margin-left: 0;


		@media (min-width: $screen-xxl-min) {
			margin: 20px;
			margin-left: 0;
		}

		&--invisible-lg {
			@media (min-width: $screen-lg-min) {
				display: none;
			}
		}

		#Group-Copy > g {
			fill: #9e9e9e;
			transition: 0.4s ease;
		}

		&.active,
		&:hover,
		&:focus {
			#Group-Copy > g {
				fill: inherit;
			}
		}
	}
}