.stage {
	position: relative;
	// width: 130px;
	margin: 0 auto;
	padding: 0;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;

	@media (min-width: $screen-md-min) {
		// width: 180px;
	}

	@media (min-width: $screen-lg-min) {
		// width: 250px;
	}

	@media (min-width: 1600px) {
		// width: 350px;
	}

	@media (min-width: $screen-xxl-min) {
		transform: translateY(-20%) scale(1.4);
	}

	&__back {
		// width: 100%;
		height: 26.2%;
	}

	&__front {
		position: absolute;
		// left: 0;
		// bottom: 0.5%;
		// width: 100%;
		left: 50%;
	    bottom: 2.5%;
	    height: 20.8%;
	    transform: translateX(-50%);
	}

	&__hole {
		position: absolute;
		bottom: 15.5%;
		left: 0;
		right: 0;
		width: 100%;
		margin: 0 auto;

		@media (min-width: $screen-md-min) {
			bottom: 18.5%;
		}

		@media (min-width: $screen-lg-min) {
			bottom: 22.3%;
		}

		svg {
			margin: 0 auto;
			width: 65%;
		}
	}

	&__bottle {
		position: absolute;
		bottom: 30%;
		left: 0;
		right: 0;
		margin: 0 auto;
		// width: 25%;
		height: 45%;
		transition: 0.6s ease;
		z-index: 0;

		&--moved {
			transform: translateY(1000px);
		}
	}

	&__light {
		position: absolute;
		bottom: 30%;
		left: 0;
		right: 0;
		margin: 0 auto;
		// width: 25%;
		height: 23%;
		transition: 0.6s ease;
		animation: HideLight 8s ease infinite;
		z-index: 1;

		&--moved {
			transform: translateY(1000px);
		}

		&--green {
			animation-delay: 0s;
		}

		&--red {
			animation-delay: 2s;
		}

		&--purple {
			animation-delay: 4s;
		}

		&--pink {
			animation-delay: 6s;
		}
	}

	&__glass {
		position: relative;
		// width: 100%;
		height: 71%;
	    transform: translateY(18%);
		// margin-bottom: -28%;
		z-index: 10;
	}

	.cover {
		transition: 0.15s ease;

		&__shadow {
			transition: 0.15s ease;
		}
	}
}

@keyframes HideLight {
	0% {
		opacity: 1;
	}

	25% {
		opacity: 0;
	}

	75% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}