.advantage {
	margin: 10px 0;
	font-family: $font-main;
	font-display: swap;
	font-size: 1rem;

	@media (min-width: $screen-md-min) {
		margin: 20px 0;
	}

	&--narrow {
		margin: 0;

		.advantage__img {
			width: 68%;

			@media (min-width: 1600px) {
				width: 65%;
			}

			@media (min-width: $screen-lg-min) {
				width: 90%;
			}
		}

		@media (min-width: $screen-md-min) {
			margin: 20px;
		}
	}

	&__img {
		display: block;
		width: 100%;
		margin: 0 auto;

		&--small {
		    width: 40%;
		}
	}

	&__text {
		color: #fff;
		text-align: center;
	}
}