@mixin color-range() {
	&--dark-blue {
		color: $color-dark-blue;
	}

	&--blue {
		color: $color-blue;
	}

	&--red {
		color: $color-red;
	}

	&--green {
		color: $color-green;
	}

	&--yellow {
		color: $color-yellow;
	}

	&--orange {
		color: $color-orange;
	}

	&--purple {
		color: $color-purple;
	}

	&--gold {
		color: $color-gold;
	}
}

@mixin color-range-pseudo() {
	&-green {
		&:after {
			color: $color-green;
		}
	}

	&-yellow {
		&:after {
			color: $color-yellow;
		}
	}

	&-blue {
		&:after {
			color: $color-blue;
		}
	}

	&-purple {
		&:after {
			color: $color-purple;
		}
	}

	&-gray {
		&:after {
			color: #a7905f;
		}
	}

	&-white {
		&:after {
			color: #fff;
		}
	}

	&-red {
		&:after {
			color: $color-red;
		}
	}
}